import _ from 'lodash';
import types from './mutation-types';

export default {
  [types.MODIFY_SERVICE_STATE_AS_KEY_VALUE](state, payload) {
    const keys = Object.keys(payload) || [];

    for (let i = 0; i <= _.size(keys); i += 1) {
      state[keys[i]] = payload[keys[i]];
    }
  },
  [types.STATUS_GET.REQUEST](state) {
    state.status = {};
  },
  [types.STATUS_GET.SUCCESS](state, payload) {
    const { status } = payload;

    state.status = status;
  },
  [types.STATUS_GET.FAIL](state) {
    state.status = {};
  },
};



