<template>
  <default-layout>
    <div slot="page">
      <div class="container mt-5">
      <b-jumbotron header="Reindexer Face" :lead="version" >
        <p :class="{ 'text-danger': isUnavailable, 'text-success': !isUnavailable }"><b>{{ onOffStatus }}</b></p>
        <br />
        <p><b>Start time</b>: {{ startTimeFormatted }}</p>
        <p><b>Uptime</b>: {{ uptimeFormat(uptime) }}</p>
        <p><b>Allocated</b>: {{ bytesToSize(status.current_allocated_bytes) }}</p>
        <p><b>Heap size</b>: {{ bytesToSize(status.heap_size) }}</p>
        <p><b>Pageheap free</b>: {{ bytesToSize(status.pageheap_free) }}</p>
        <p><b>Pageheap unmapped</b>: {{ bytesToSize(status.pageheap_unmapped) }}</p>
      </b-jumbotron>
      </div>
    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import * as moment from 'moment';
import DefaultLayout from '@/layouts/DefaultLayout';
import { mapActions, mapState } from 'vuex';
import bytesToSize from '@/utils/bytesToSize';
import uptimeFormat from '@/utils/uptimeFormat';

export default {
  name: 'MainPage',

  components: {
    DefaultLayout,
  },

  data() {
    const refreshStatusIntervalMs = 5000;
    const clockTick = 1000;

    return {
      refreshStatusIntervalMs,
      clockTick,
      refreshIntervalDescriptor: null,
      uptimeIncrementDescriptor: null,
      isUnavailable: false,
    };
  },

  computed: {
    ...mapState({
      status: state => state.service.status,
      version: state => state.service.version,
      uptime: state => state.service.uptime,
      startTime: state => state.service.startTime,
    }),

    onOffStatus() {
      return this.isUnavailable ? 'Offline' : 'Online';
    },

    startTimeFormatted() {
      return moment.unix(this.startTime).format('DD.MM.YYYY HH:MM:ss');
    },
  },

  methods: {
    ...mapActions({
      getStatus: 'service/getStatus',
      modifyServiceStateAsKV: 'service/modifyStateAsKV',
    }),

    bytesToSize,
    uptimeFormat,

    getStatusAndSync() {
      this.getStatus().then(() => {
        clearInterval(this.uptimeIncrementDescriptor);

        this.isUnavailable = false;
        const newStatusState = {};

        if (this.uptime < this.status.uptime) {
          newStatusState.uptime = this.status.uptime;
        }

        if (!_.isEmpty(this.status.version)) {
          newStatusState.version = this.status.version;
        }

        if (!_.isNil(this.status.start_time)) {
          newStatusState.startTime = this.status.start_time;
        }

        this.modifyServiceStateAsKV(newStatusState);

        this.uptimeIncrementDescriptor = setInterval(this.uptimeIncrement, this.clockTick);
      }).catch(() => {
        this.isUnavailable = true;
        clearInterval(this.uptimeIncrementDescriptor);
      });
    },

    uptimeIncrement() {
      this.modifyServiceStateAsKV({ uptime: this.uptime + 1 });
    },
  },

  mounted() {
    this.getStatusAndSync();

    this.refreshIntervalDescriptor = setInterval(this.getStatusAndSync,
      this.refreshStatusIntervalMs);
  },

  destroyed() {
    clearInterval(this.refreshIntervalDescriptor);
    clearInterval(this.uptimeIncrementDescriptor);
  },
};
</script>



