<template>
  <div>
    <preloader v-model="isLoadingProgress" />
    <b-alert show v-if="warningMessage">{{ warningMessage }}</b-alert>
  </div>
</template>

<script>
import _ from 'lodash';
import Preloader from '@/components/Preloader';

export default {
  name: 'PageStatusNotifier',

  components: {
    Preloader,
  },

  props: {
    currentDatabaseName: {
      type: String,
      default: null,
    },

    currentNamespaceName: {
      type: String,
      default: null,
    },

    isLoadingProgress: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    warningMessage() {
      if (!this.isLoadingProgress && _.isEmpty(this.currentDatabaseName)) {
        return 'Create a database';
      } else if (!_.isNull(this.currentNamespaceName) && _.isEmpty(this.currentNamespaceName)
        && _.size(this.currentDatabaseName)) {
        return 'Select a namespace';
      }

      return null;
    },
  },
};
</script>



