<template>
  <b-modal v-model="isShown" title="Index" @hide="onWindowHide">
    <b-container fluid>
      <b-row class="mb-1">
        <b-col cols="2" class="col-sm-4 mt-2">
          <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.name' name="question" />name
        </b-col>
        <b-col>
          <b-input
            :state="Boolean(indexBody.name)"
            v-model="indexBody.name"
            @input="onNameInput"
          />
        </b-col>
      </b-row>
      <b-row class="mb-1">
        <b-col cols="2" class="col-sm-4 mt-2">
          <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.jsonPath' name="question" />json_path
        </b-col>
        <b-col>
          <b-input
            :state="Boolean(indexBody.jsonPath) || indexBody.fieldType=='composite'"
            v-model="indexBody.jsonPath"
          />
        </b-col>
      </b-row>
      <b-row class="mb-1">
        <b-col cols="2" class="col-sm-4 mt-2">
          <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.fieldType' name="question" />field_type
        </b-col>
        <b-col>
          <b-form-select
            :state="Boolean(indexBody.fieldType)"
            id="field_type"
            :value="null"
            v-model="indexBody.fieldType"
            :options="['int', 'int64', 'double', 'string', 'bool', 'composite']"
          />
        </b-col>
      </b-row>
      <b-row class="mb-1">
        <b-col cols="2" class="col-sm-4 mt-2">
          <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.indexType' name="question" />index_type
        </b-col>
        <b-col>
          <b-form-select
            :state="Boolean(indexBody.indexType)"
            id="index_type"
            :value="null"
            v-model="indexBody.indexType"
            :options="['hash', 'tree', 'text', '-']"
          />
        </b-col>
      </b-row>
      <b-row class="mb-1">
        <b-col cols="2" class="col-sm-4 mt-2">
          <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.options' name="question" />options
        </b-col>
        <b-col class="text-center">
          <label class="mt-2"><input type="checkbox" v-model="indexBody.isPk">Pk</label>
          <label class="mt-2 ml-2"><input type="checkbox" v-model="indexBody.isArray">Array</label>
          <label class="mt-2 ml-2"><input type="checkbox" v-model="indexBody.isDense">Dense</label>
          <label class="mt-2 ml-2"><input type="checkbox" v-model="indexBody.isSparse">Sparse</label>
        </b-col>
      </b-row>
      <b-row class="mb-1">
        <b-col cols="2" class="col-sm-4 mt-2">
          <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.collateMode' name="question" />collate_mode
        </b-col>
        <b-col>
          <b-form-select
            :state="Boolean(indexBody.collateMode)"
            :value="null"
            v-model="indexBody.collateMode"
            :options="['none', 'ascii', 'utf8', 'numeric', 'custom']" />
        </b-col>
      </b-row>
      <b-row v-if="indexBody.collateMode == 'custom'" class="mb-1">
        <b-col cols="3" class="col-sm-5 mt-2">
          <octicon class="mr-1 icon_tooltip" v-b-tooltip :title='tooltips.sortOrderLetters' name="question" />sort_order_letters
        </b-col>
        <b-col>
          <b-input
            :state="Boolean(indexBody.sortOrderLetters)"
            v-model="indexBody.sortOrderLetters"
          />
        </b-col>
      </b-row>
    </b-container>
    <div slot="modal-footer" class="w-100">
      <b-button :disabled="!isValid" size="sm" class="float-right" variant="success" @click.prevent="onConfirm(isModifying)">
        {{ isModifying ? 'Update' : 'Create' }}
      </b-button>
    </div>
  </b-modal>
</template>

<script>
import Octicon from 'vue-octicon/components/Octicon';
import tooltips from './tooltips';

export default {
  name: 'IndexModifyWindow',

  components: {
    Octicon,
  },

  props: {
    value: {
      type: Boolean,
      default: true,
    },
    index: {
      type: Object,
      required: true,
    },
    isModifying: {
      type: Boolean,
      default: true,
    },
  },

  data() {
    const isShown = this.$props.value;
    const indexBody = this.$props.index;

    return {
      tooltips,
      isShown,
      indexBody,
    };
  },

  computed: {
    isValid() {
      const { name, jsonPath, fieldType, indexType, collateMode,
      sortOrderLetters } = this.indexBody;
      const isValid = name && jsonPath && fieldType && indexType && collateMode;

      if (this.indexBody.collateMode === 'custom') {
        return Boolean(isValid && sortOrderLetters);
      }

      return Boolean(isValid);
    },
  },

  methods: {
    onNameInput(value) {
      this.indexBody.jsonPath = value;
      this.$emit('onIndexChange', this.indexBody);
    },

    onConfirm(isModifying) {
      this.$emit(isModifying ? 'onUpdate' : 'onCreate', this.indexBody);
    },

    onWindowHide() {
      this.$emit('onWindowHide');
    },
  },

  watch: {
    value(newValueProp) {
      this.isShown = newValueProp;
    },
    index(newIndexProp) {
      this.indexBody = newIndexProp;
    },
  },
};
</script>

<style scoped>
.icon_tooltip {
  position: relative;
  top: 3px;
  right: 3px;
}

.icon_tooltip:focus {
  outline: none;
  pointer-events: none;
}
</style>



