<template>
  <default-layout>
    <div slot="page">
      <IndexModifyWindow
        v-model="indexModifyWindowIsShown"
        :index="index"
        :isModifying="isIndexModifying"
        @onCreate="onIndexCreate"
        @onUpdate="onIndexUpdate"
        @onWindowHide="onModifyIndexWindowHide"
      />

      <PageStatusNotifier
        :isLoadingProgress="isLoading"
        :currentDatabaseName="db"
        :currentNamespaceName="namespace"
      />

      <!-- TODO refactoring: move to a new datatable component-->
      <b-container v-if="namespaceOneExists" fluid>
        <b-btn class="mb-3" size="sm" variant="success" @click="onIndexAddClick">Add index</b-btn>

          <div class="table-responsive">
          <table class="table table-sm table-bordered table-striped ">
          <thead>
            <tr class="d-flex">
              <th class="col-sm-3">Index</th>
              <th class="col-sm-2">Field Json Path</th>
              <th class="col-sm-1">Field Type</th>
              <th class="col-sm-1">Index Type</th>
              <th class="col-sm-4">Options</th>
              <th class="col-sm-1">Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr class="d-flex" v-for="indexWithLabels, indexKey in indexesWithLabels" :key="indexKey">
              <td class="col-sm-3"><div class="cellContent">{{indexWithLabels.name}}</div></td>
              <td class="col-sm-2"><div class="cellContent">{{indexWithLabels.jsonPath}}</div></td>
              <td class="col-sm-1">{{indexWithLabels.fieldType}}</td>
              <td class="col-sm-1">{{indexWithLabels.indexType}}</td>
              <td class="col-sm-4"><div class="cellContent">
                <b-badge class="mr-3" v-for="option, indexKey in indexWithLabels.options" :key="indexKey" pill :variant="option.label">{{option.value}}</b-badge>
              </div></td>
              <td class="col-sm-1">
                <b-button
                  variant="primary"
                  size="sm"
                  class="mr-1"
                  @click.prevent="onUpdateIndexClick(indexKey)"><octicon name="pencil" /></b-button>

                <b-button
                  variant="danger"
                  size="sm"
                  class="mr-1"
                  @click.prevent="() => {onIndexDeleteQuestion({name: indexWithLabels.name})}"><octicon name="trashcan" /></b-button>
              </td>
            </tr>
          </tbody>
        </table>
        </div>
      </b-container>

    </div>
  </default-layout>
</template>

<script>
import _ from 'lodash';
import { mapState, mapGetters } from 'vuex';
import DefaultLayout from '@/layouts/DefaultLayout';
import Octicon from 'vue-octicon/components/Octicon';
import PageStatusNotifier from '@/components/PageStatusNotifier';
import IndexModifyWindow from '@/components/Modal/IndexModify/IndexModifyWindow';
import DataTable from '@/components/DataTable';

export default {
  name: 'IndexesPage',

  components: {
    DefaultLayout,
    Octicon,
    PageStatusNotifier,
    IndexModifyWindow,
    DataTable,
  },

  data() {
    const indexTemplate = {
      name: '',
      jsonPath: '',
      fieldType: '',
      indexType: '',
      collateMode: '',
      sortOrderLetters: '',
      isPk: false,
      isArray: false,
      isDense: false,
      isAppendable: false,
      isSparse: false,
    };

    return {
      newIndexTemplate: {
        ...indexTemplate,
      },

      index: {
        ...indexTemplate,
      },

      indexModifyWindowIsShown: false,
      isIndexModifying: false,
    };
  },

  computed: {
    ...mapState({
      db: state => state.settings.database,
      namespace: state => state.settings.namespace,
      // TODO refactoring rename namespaceOne to currentNamespace or smth else
      namespaceOne: state => state.namespaces.one,
      isLoading: state => state.namespaces.isLoading,
    }),

    ...mapGetters({
      indexes: 'namespaces/indexes',
      fields: 'namespaces/indexFields',
    }),

    namespaceOneExists() {
      return !_.isEmpty(this.namespaceOne);
    },

    indexesSize() {
      return _.size(this.indexes);
    },

    selectedFields() {
      return _.cloneDeep(this.fields);
    },

    // TODO refactoring: we need this because we haven't a new correct datatable component
    indexesWithLabels() {
      return this.indexes.map(idx => ({
        name: idx.name,
        fieldType: idx.field_type,
        jsonPath: idx.json_path,
        indexType: idx.index_type,
        options: [{
          label: 'danger',
          value: idx.is_pk ? 'PK' : '',
        }, {
          label: 'success',
          value: idx.is_dense ? 'Dense' : '',
        }, {
          label: 'primary',
          value: idx.is_array ? 'Array' : '',
        }, {
          label: 'info',
          value: idx.is_sparse ? 'Sparse' : '',
        }, {
          label: 'warning',
          value: !_.isEmpty(idx.collate_mode) && idx.collate_mode !== 'none' ? `Collate: ${idx.collate_mode}` : '',
        }, {
          label: 'secondary',
          value: !_.isEmpty(idx.sort_order_letters) ? `Sort order letters: "${idx.sort_order_letters}"` : '',
        }],
      }));
    },
  },

  methods: {
    onIndexAddClick() {
      this.isIndexModifying = false;
      this.indexModifyWindowIsShown = true;
    },

    onUpdateIndexClick(indexKey) {
      const index = this.indexes[indexKey];
      const camelCasedIndex = _.mapKeys(index, (value, key) => _.camelCase(key));
      this.index = {
        ...this.indexTemplate,
        ...camelCasedIndex,
      };

      this.isIndexModifying = true;
      this.indexModifyWindowIsShown = true;
    },

    onIndexCreate() {
      const pload = {
        db: this.db,
        namespace: this.namespace,
        index: this.index,
      };

      this.$store.dispatch('namespaces/createIndex', pload).then(() => {
        this.indexModifyWindowIsShown = false;
      });
    },

    onIndexUpdate() {
      const pload = {
        db: this.db,
        namespace: this.namespace,
        index: this.index,
      };

      this.$store.dispatch('namespaces/updateIndex', pload).then(() => {
        this.indexModifyWindowIsShown = false;
      });
    },

    onIndexDeleteQuestion(index) {
      const { name } = index;

      this.$modal.show({
        header: 'Are you sure?',
        message: 'This action will cause a removal of the index:',
        strongMessage: name,
        event: 'onIndexDelete',
        eventParams: name,
      });
    },


    onModifyIndexWindowHide() {
      this.indexModifyWindowIsShown = false;
    },

    onIndexModifyConfirm() {
      this.onCreateIndexClick();
    },
  },

  watch: {
    namespaceOne(newNamespaceOne) {
      if (!_.isEqual(this.namespaceOne, newNamespaceOne)) {
        this.index = _.copyDeep(this.newIndexTemplate);
      }
    },
  },
};
</script>

<style scoped>
.cellContent {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
</style>



