import pluralize from 'pluralize';

export default function uptimeFormat(secondsNum) {
  const days = Math.floor(secondsNum / 86400);
  const hours = Math.floor((secondsNum - (days * 86400)) / 3600);
  const minutes = Math.floor((secondsNum - (days * 86400) - (hours * 3600)) / 60);
  const seconds = secondsNum - (days * 86400) - (hours * 3600) - (minutes * 60);

  const daysStr = days > 0 ? `${pluralize('day', days, true)} ` : '';
  const hoursStr = hours < 10 ? `0${hours}` : `${hours}`;
  const minutesStr = minutes < 10 ? `0${minutes}` : `${minutes}`;
  const secondsStr = seconds < 10 ? `0${seconds}` : `${seconds}`;

  return `${daysStr} ${hoursStr}:${minutesStr}:${secondsStr}`;
}



