import _ from 'lodash';
import pluralize from 'pluralize';

export default function bytesToSize(bytes) {
  const sizePrefixes = ['', 'K', 'M', 'G', 'T'];

  if (bytes === 0 || bytes === '0') {
    return '0 Byte';
  }

  const i = parseInt(Math.floor(Math.log(bytes) / Math.log(1024)), 10);
  const count = Math.round(bytes / (1024 ** i), 2) || 0;
  const measureName = _.isEmpty(sizePrefixes[i]) ? 'Byte' : `${sizePrefixes[i]}byte`;

  return pluralize(measureName, count, true);
}



