import Api from '@/api';
import routes from '@/api/routes';
import types from './mutation-types';

export const modifyStateAsKV = (context, payload) => {
  const { commit } = context;

  commit(types.MODIFY_SERVICE_STATE_AS_KEY_VALUE, payload);
};

export const getStatus = (context, params) => {
  const { commit } = context;

  return new Promise((resolve, reject) => {
    Api.get(routes.status, params)
      .then(
        (response) => {
          const { data } = response;
          const payload = {
            status: data,
          };

          commit(types.STATUS_GET.SUCCESS, payload);
          resolve();
        },
        (error) => {
          commit(types.STATUS_GET.FAIL, error);
          reject();
        },
    ).catch((error) => {
      commit(types.STATUS_GET.FAIL, error);
      reject();
    });
  });
};

export default {
  modifyStateAsKV,
  getStatus,
};



